}
.login-container {
    margin: 50px auto;
    width: 500px;
    padding: 50px;
    text-align: center;
    box-shadow: 0px 0px 11px 2px rgba(255,0,0,0.78);
}
.main-logo {
    width: 600px;
}
.select-location-container {
    margin: 50px auto;
    width: 500px;
